var me ;
export default {
	data() {
		return {
			input:"",
			tuishuList:[],
			temp: [],
			InputBottom: 0,
			id: ""
		}
	},
	onLoad(data) {
		me = this;
		me.id = data.id;
		uni.showLoading({
			mask: true,
			title: "请稍后..."
		});
		me.refresh({success(title) {
			uni.setNavigationBarTitle({
				title: title
			})
		}});
	},
	onPullDownRefresh() {
		this.refresh()
	},
	methods: {
		refresh(a){
			
			uni.request({
				url: me.serverUrl+"/qiushu/getQiuShuAndPinglunByQiuShuId?qiushuId=" + me.id,
				method: "GET",
				success(res) {
					me.temp = [];
					var temp = res.data.data;
					me.temp = temp;
					console.log(temp);
					a.success(temp.title)
				},
				complete: () => {
					uni.hideNavigationBarLoading();
					uni.hideLoading();
					uni.stopPullDownRefresh();
				}
			})
		},
		addQiushu(){
			
			uni.request({
				url: me.serverUrl+"/qiushu/addPinglun" ,
				data:{
					"qiushuId": me.temp.id,
					"userId": me.temp.userId,
					"content": me.input
					
				},
				method: 'POST',
				
				success(res) {
					me.refresh();
					
					console.log("success")
				}
			})
		},
		InputFocus(e) {
			this.InputBottom = e.detail.height
		},
		InputBlur(e) {
			this.InputBottom = 0
		}
	}
}
